@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
html, body, #root, #click-zone {
    height: 100%
}

#screen {
    height: 100%;
    width: 100%;
}
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    background-color: gray;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.start-zone {
    width: 800px;
    height: 600px;
    margin: auto;
    padding-top: 7rem;
    position: relative;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}
.info {
    display: block;
    opacity: .8;
    width: 400px;
    font-weight: bold;
    font-size: x-large;
    /*background-color: white;*/
    margin: auto;
    padding: 1rem;
    text-align: center;
    color: black;
    position: relative;
    z-index: 3;
}

.start {
    /*display: block;*/
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 50%;  /* position the top  edge of the element at the middle of the parent */
    left: 50%;
    transform: translate(-50%, -50%);
    border: none;
    padding: 10px;
    border-radius: 7px;
    font-size: large;
    /*z-index: 2;*/
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.game-zone-balloons {
    width: 800px;
    height: 600px;
    margin: auto;
    /*border: 5px solid black;*/
    position: relative;
    background-color: #007ced;
    background: linear-gradient(to bottom, #007ced 1%,#cce7ff 100%);
    overflow: hidden;
}

#cloud-intro {
    position: relative;
    height: 100%;
    background: url(https://static.radulescu.me/examples/clouds/clouds1000.png);
    background: url(https://static.radulescu.me/examples/clouds/clouds1000.png) 0 200px,
    url(https://static.radulescu.me/examples/clouds/clouds1200_1.png) 0 300px,
    url(https://static.radulescu.me/examples/clouds/clouds1000_blur3.png) 100px 250px;
    animation: wind 30s linear infinite;
}

@keyframes wind{
    0% {
        background-position: 0 200px, 0 300px, 100px 250px;
    }
    100% {
        background-position: 1000px 200px, 1200px 300px, 1100px 250px;
    }

}

.wrapper {
    display: inline-block;
    position: absolute;
    top: 600px;
    /*transition: top 6s;*/
    transition-timing-function: ease-in;
}

.up {
    top: -200px;
    /*transition: top 6s;*/
}

.balloon {
    position: absolute;
    border-radius: 80%;
    width: 120px;
    height: 120px;
    /*background-color: #ffc0cb;*/
    /*box-shadow: 10px 5px 5px rgba(211, 211, 211, .5);*/
    /*border: solid rgba(211, 211, 211, .5);*/
    text-align: center;
    justify-content: center;
    align-items: center;
    box-shadow:inset -5px -7px 10px rgba(0,0,0,.1);
}

.balloon:before {
    content:"";
    display:block;
    position:absolute;
    width:50%;
    height:50%;
    top:7.5%;
    left:7.5%;
    border-radius: 75% 25%;
    box-shadow:inset 10px 10px 50px rgba(255,255,255,.5);
}

.wrapper h1 {
    margin-top: 35px;
    font-weight: 900;
    color: green;
    font-family: 'Patrick Hand', cursive;
}

.pop {
    width: 100px;
    height: 150px;
    transition: none;
    background-image: url(/wild-gun/static/media/explosion.89f88eba.svg);

}

.explosion {
    /*position: absolute;*/
    width: 150px;
    height: 150px;
    margin-top: -30px;
    margin-left: 5px;
    z-index: 3;
    background-image: url(/wild-gun/static/media/explosion.89f88eba.svg);
    background-size:contain;

    /*transform: scale(1);*/
}

